.Input {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.Label {
  font-weight: bold;
  display: block;
  margin-bottom: 8px;
}

.InputElement {
  outline: none;
  border: 1px solid #cccccc;
  background-color: #ffffff;
  font: inherit;
  padding: 6px 10px;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.InputElement:focus {
  outline: none;
  background-color: #cccccc;
}

.Invalid {
  border: 1px solid rgb(255, 0, 0);
  background-color: rgb(252, 176, 167);
}

.ValidationError {
  color: red;
  margin: 5px 0;
}
